<template>
  <main class="content">
    <div
      class="dropdown-menu dropdown-menu-sm"
      id="context-menu3"
      onmouseleave="this.style.display='none'"
    >
      <a class="dropdown-item" data-toggle="modal" data-target="#yeninoktaModal" id="yeninokta">
        <i class="fas fa-pencil-alt"></i> Yeni Ağac
      </a>
      <a
        class="dropdown-item"
        data-toggle="modal"
        data-target="#yenicihazModal"
        id="yenicihaz"
        @click="addproceessbegin"
      >
        <i class="fas fa-cog"></i> Yeni İşlem
      </a>
      <a class="dropdown-item" data-toggle="modal" data-target="#sileminModal" id="noktasillink">
        <i class="fas fa-bell"></i> Sil
      </a>
      <a
        class="dropdown-item"
        data-toggle="modal"
        data-target="#isimdegisModal"
        id="noktaisimdegis"
      >
        <i class="fas fa-pencil-alt"></i> İsim Değiştir
      </a>
    </div>

    <div
      class="dropdown-menu dropdown-menu-sm"
      id="context-menu"
      onmouseleave="this.style.display='none'"
    >
      <a
        class="dropdown-item"
        data-toggle="modal"
        data-target="#yenicihazModal"
        @click="edittreenode"
      >
        <i class="fas fa-pencil-alt"></i> Düzenle
      </a>
      <a class="dropdown-item" data-toggle="modal" data-target="#sileminModal">
        <i class="fas fa-bell"></i> Sil
      </a>
    </div>

    <div class="container-fluid p-0">
      <div class="page-title-right">
        <ol class="breadcrumb m-0">
          <li class="breadcrumb-item">
            <a href="javascript: void(0);">Anasayfa</a>
          </li>
          <li class="breadcrumb-item active">Bom Ağacı</li>
        </ol>
      </div>
      <br />
      <h1 class="h3 mb-3">Bom Ağacı</h1>
      <div class="row">
        <div class="col-12">
          <div class="card">
            <div class="card-body">
              <div class="row">
                <div class="col-xl-6">
                  <div class="form-group">
                    <label for="projectname">Proje Kodunu Seçiniz</label>
                    <select class="form-control" v-model="project" @change="gettree">
                      <option selected disabled value>Proje kodunu seçiniz</option>
                      <option
                        v-for="item in projects"
                        :key="item.Id"
                        :value="item.StokCode"
                      >{{item.Name}}</option>
                    </select>
                  </div>
                </div>
              </div>
                 <div class="mb-3 center" v-if="loadmi">
										<button class="btn btn-primary" @click="exportTable" >Ağacı Exel olarak dışa aktar</button>
							  	</div>
              <div class="row">
                 <div class="col-md-6">
                  <div hidden id="spinner"></div>
                  <tree-view
                    v-show="iftree"
                    :tree="tree"
                    @click="deneme"
                    @spanclickevent="deneme"
                    @contextmenu="contextmenu"
                    @divclickevent="divclickevent"
                    @ondrop="ondrop"
                  ></tree-view>
                </div>
                <div id="seriolan" class="col-md-6" v-show="ifserinumber">
                  <label>{{Usttanim}}</label>
                  <form id="loginform">
                    <template v-if="serimi">
                      <div class="form-group">
                        <label>Seri Numarası</label>
                        <input
                          type="text"
                          class="form-control"
                          placeholder="Seri Numarasını Giriniz"
                          v-model="seritext"
                          required
                        />
                      </div>
                      <div class="form-group" v-if="ipmi">
                        <label>İp Adresi</label>
                        <input
                          type="text"
                          class="form-control"
                          placeholder="İp Adresini Giriniz"
                          v-model="ipaddress"
                          required
                        />
                      </div>
                    </template>
                    <div class="custom-control custom-checkbox form-group" v-else>
                      <input
                        type="checkbox"
                        class="custom-control-input"
                        id="customControlAutosizing"
                        v-model="altyapicheck"
                      />
                      <label
                        class="custom-control-label"
                        for="customControlAutosizing"
                      >İşlem Tamamlandı mı?</label>
                    </div>
                    <div class="form-group">
                      <div class="custom-file">
                        <input
                          type="file"
                          class="custom-file-input"
                          ref="myFiles"
                          @change="previewFiles"
                          id="validatedCustomFile"
                          required
                        />
                        <label class="custom-file-label" for="validatedCustomFile">{{value}}</label>
                      </div>
                      <hr />
                      <div class="form-group mb-0 text-center">
                        <button class="btn btn-primary mr-4" type="submit" @click="kaydet">Kaydet</button>
                        <button
                          class="btn btn-warning"
                          type="submit"
                          @click="ifserinumber=false"
                        >İptal</button>
                      </div>
                    </div>
                  </form>
                  <!-- end col-->
                </div>

                <!-- end row -->
              </div>

              <!-- end row -->
            </div>
            <!-- end card-body -->
          </div>
          <!-- end card-->
        </div>
        <!-- end col-->
      </div>
    </div>

    <!-- İsim Değiş Modal-->
    <div
      class="modal fade"
      id="isimdegisModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-md" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Nokta Adı Değiştirme</h5>
            <button class="close" type="button" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">×</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group row">
              <label for="recipient-name" class="col-sm-3 col-form-label">Nokta Adı:</label>
              <div class="col-sm-9">
                <input
                  type="Text"
                  class="form-control"
                  id="isimdegistext"
                  name="isimdegistext"
                  value
                  required
                />
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              id="isimdegiskapat"
              class="btn btn-secondary"
              type="button"
              data-dismiss="modal"
            >İptal</button>
            <a
              class="btn btn-primary"
              href="#"
              id="isimdegislink"
              data-noktaid
              onclick="isimdegisfunc(this.dataset.noktaid,'admin/isimdegis')"
            >Uygula</a>
          </div>
        </div>
      </div>
    </div>
    <!-- İsim Değiş Modal-->

    <!-- Yeni nokta Modal-->
    <div
      class="modal fade"
      id="yeninoktaModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-md" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Yeni Nokta Ekle</h5>
            <button class="close" type="button" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">×</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group row">
              <label for="recipient-name" class="col-sm-3 col-form-label">Ağaç Stok:</label>
              <div class="col-sm-9">
                <input type="Text" class="form-control" v-model="newtreestok" required />
              </div>
            </div>
            <div class="form-group row">
              <label for="recipient-name" class="col-sm-3 col-form-label">Ağaç Adı:</label>
              <div class="col-sm-9">
                <input type="Text" class="form-control" v-model="newtreestokname" required />
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              class="btn btn-secondary"
              type="button"
              data-dismiss="modal"
              id="addewtreenodecancel"
            >İptal</button>
            <a class="btn btn-primary" href="#" @click="addtreenode">Kaydet</a>
          </div>
        </div>
      </div>
    </div>
    <!-- yeni nokta Modal-->

    <!-- yeni işlem Modal-->
    <div
      class="modal fade"
      id="yenicihazModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-md" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{editandnewheader}}</h5>
            <button class="close" type="button" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">×</span>
            </button>
          </div>
          <div class="modal-body">
            <form id="yenicihazform">
              <div class="form-group row">
                <label for="recipient-name" class="col-sm-2 col-form-label">Stok kodu:</label>
                <div class="col-sm-10">
                  <input type="text" class="form-control" v-model="newtreestok" required />
                </div>
              </div>

              <div class="form-group row">
                <label for="recipient-name" class="col-sm-2 col-form-label">Tanım:</label>
                <div class="col-sm-10">
                  <input type="text" class="form-control" v-model="newtreestokname" required />
                </div>
              </div>
              <div class="custom-control custom-checkbox form-group">
                <input
                  type="checkbox"
                  class="custom-control-input"
                  id="newserinumbercontrol"
                  v-model="newserinumbercontrol"
                />
                <label
                  class="custom-control-label"
                  for="newserinumbercontrol"
                >Seri numarası takibi varmı?</label>
              </div>
              <div class="custom-control custom-checkbox form-group">
                <input
                  type="checkbox"
                  class="custom-control-input"
                  id="newipcontrol"
                  v-model="newipcontrol"
                />
                <label class="custom-control-label" for="newipcontrol">İp adresi varmı?</label>
              </div>
              <div class="custom-control custom-checkbox form-group">
                <input
                  type="checkbox"
                  class="custom-control-input"
                  id="newimalatcontrol"
                  v-model="newimalatcontrol"
                />
                <label class="custom-control-label" for="newimalatcontrol">İmalat Takibi varmı?</label>
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button
              class="btn btn-secondary"
              id="addewtreeproceescancel"
              type="button"
              data-dismiss="modal"
            >İptal</button>
            <template v-if="addmi">
              <a class="btn btn-primary" @click="addtreeprocess">Kaydet</a>
            </template>
            <template v-else>
              <a class="btn btn-primary" @click="edittreenodesent">Güncelle</a>
            </template>
          </div>
        </div>
      </div>
    </div>
    <!-- yeni işlem Modal-->

    <!-- silemin Modal-->
    <div
      class="modal fade"
      id="sileminModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">İşlemi Ağaçtan Silmek Üzeresiniz?</h5>
            <button class="close" type="button" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">×</span>
            </button>
          </div>
          <div class="modal-body">İşlemi Ağaçtan Silmek istediğinize Emin misiniz?</div>
          <div class="modal-footer">
            <button
              id="silemincancel"
              class="btn btn-secondary"
              type="button"
              data-dismiss="modal"
            >İptal</button>
            <a class="btn btn-primary" @click="deleteprocess">Sil</a>
          </div>
        </div>
      </div>
    </div>
    <!-- silemin Modal-->
  </main>
</template>

<script>
import Swal from "sweetalert2";
import XLSX from "xlsx";
import saveAs  from 'file-saver';
import  state from "../../Helper/custom_state"
export default {
  data() {
    return {
      project: "",
      value: "Resim seç",
      selectedFile: "",
      tree: [],
      ifserinumber: false,
      seritext: "",
      serimi: false,
      altyapicheck: false,
      selectedid: 0,
      iftree: false,
      Usttanim: "",
      projects: JSON.parse(localStorage.getItem("projects")),
      dataspinner: "",
      selectedStokCode: "",
      newtreestok: "",
      newtreestokname: "",
      selectedStokCodeTanim: "",
      selectedElement: "",
      newimalatcontrol: "",
      newipcontrol: "",
      newserinumbercontrol: "",
      ipmi: false,
      ipaddress: "",
      selecteddeleteid: 0,
      editandnewheader: "",
      addmi: false,
      loadmi:false,
    };
  },

  methods: {
    previewFiles(event) {
      this.value = this.$refs.myFiles.files[0].name;
      this.selectedFile = event.target.files[0];
    },
    gettree: function() {
    
      this.dataspinner = document.getElementById("spinner");
      this.dataspinner.removeAttribute("hidden");
      let myself = this;
      this.$http.post(`${state.ip}/api/admin/treebomgetir`, {
          project: this.project
        })
        .then(response => {
          //let my = this.makeTree({ q: response });
          // eslint-disable-next-line no-console
          console.log(response);
          myself.tree = response.data;
          myself.iftree = true;
          myself.dataspinner.setAttribute("hidden", "");
            this.loadmi=true;
        })
        .catch(function(error) {
          // eslint-disable-next-line no-console
          console.log(error.response);
        });
    },
    divclickevent: function() {
      this.ifserinumber = false;
      if (this.selectedid > 0) {
        let selectedelement = document.getElementsByName(this.selectedid);
        selectedelement[0].style.backgroundColor = "";
      }
    },
    deneme: function(cevap) {
      this.selectedid = cevap.id;
      var element = this.tree;
      let result = this.searchTree(element[0], this.selectedid);
      this.ifserinumber = true;
      // eslint-disable-next-line no-console

      if (result.SerinoTakip === 1) {
        if (result.IpTakip === 1) {
          this.ipmi = true;
          result.Ipaddress === null
            ? (this.ipaddress = "")
            : (this.ipaddress = result.Ipaddress);
        }
        this.serimi = true;
        result.SeriNo === null
          ? (this.seritext = "")
          : (this.seritext = result.SeriNo);
      } else if (result.AltYapiTakip === 1) {
        this.serimi = false;
        if (result.SeriNo === "true") this.altyapicheck = true;
        else this.altyapicheck = false;
      } else {
        this.ifserinumber = false;
        this.seritext = "";
      }
      this.Usttanim="";
       this.searchenust(result.UstStok)
      
      let selectedelement = document.getElementsByName(this.selectedid);
      let span = document.querySelectorAll("#survey");
      for (let i = 0; i < span.length; i++) {
        span[i].style.backgroundColor = "#36404a";
      }
      selectedelement[0].style.backgroundColor = "orange";
      let form = document.getElementById("seriolan");
      form.style.marginTop = window.scrollY + "px";
      window.addEventListener("scroll", function() {
        form.style.marginTop = window.scrollY + "px";
      });
    },
     ValidateIPaddress(ipaddress) 
        {
        if (/^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/.test(myForm.emailAddr.value))
          {
             return (true)
          }
         alert("You have entered an invalid IP address!")
         return (false)
        },

    contextmenu(e) {
      e.preventDefault();

      if (e.target.tagName === "SPAN") {
        this.selectedStokCode = e.target.dataset.stokcode;
        this.selectedStokCodeTanim = e.target.dataset.tanim;
        this.selecteddeleteid = e.target.dataset.id;
        this.selectedElement = e.target.parentElement.parentElement;
        var top = e.pageY;
        var left = e.pageX;
        if (e.target.parentElement.className === "branch link") {
          $("#context-menu3")
            .css({
              display: "block",
              top: top,
              left: left
            })
            .addClass("show");
        } else {
          $("#context-menu")
            .css({
              display: "block",
              top: top,
              left: left
            })
            .addClass("show");
        }
      }
    },

    ondrop(value) {
      let element = document.getElementsByName(value.Id)[0];
      this.$http.post(`${state.ip}/api/admin/updateuststok`, {
          Id: value.Id,
          changeId: value.event.target.dataset.id
        })
        .then(response => {
          let data = this.searchTree(this.tree[0], value.Id);
          let result = this.searchstokTree(
            this.tree[0],
            element.dataset.uststok
          );
          result.nodes = result.nodes.filter(person => person.Id != value.Id);
          result = this.searchTree(this.tree[0], value.event.target.dataset.id);
          data.UstStok = result.Stokcode;
          data.UstStokTanim = result.Tanim;
          result.nodes.push(data);
        });
    },
    kaydet: function(e) {
      let myself = this;
      e.preventDefault();
      const formData = new FormData();
      if (this.serimi) {
        formData.append("serinumber", this.seritext);
        formData.append("file", this.selectedFile);
        formData.append("id", this.selectedid);
        if (this.ipmi) formData.append("ipaddress", this.ipaddress);
      } else {
        formData.append("serinumber", this.altyapicheck);
        formData.append("file", this.selectedFile);
        formData.append("id", this.selectedid);
      }
      if (
        (this.seritext !== "" && this.selectedFile !== "") ||
        (this.serimi === false && this.selectedFile !== "")
      ) {

        if(this.ValidateIPaddress){
        this.$http.post(`${state.ip}/api/admin/serinokaydet`, 
            formData
          )
          .then(response => {
            if (response.data.succes) {
              Swal.fire("", response.data.msg, "success");
              let selectedelement = document.getElementsByName(this.selectedid);
              selectedelement[0].style.color = "green";
              document.getElementById("loginform").reset();
              myself.value = "Resim seç";
              var element = myself.tree;
              let result = this.searchTree(element[0], this.selectedid);
              result.SeriNo = myself.seritext;
              result.Ipaddress = myself.ipaddress;
              this.searchstokTree(element[0], result.UstStok).progress.count++;
              this.ifserinumber = false;

              // result.SeriNo = myself.seritext;
            } else {
              Swal.fire("", response.data.msg, "error");
            }
          })
          .catch(function(error) {
            // eslint-disable-next-line no-console
            console.log(error.response);
          });
          }
          else{
            Swal.fire("", "Geçerli bir ip adresi giriniz.", "error");
          }
      } else {
        Swal.fire("", "Tüm alanlar doldurulmalıdır.", "error");
      }
    },
    searchTree(element, id) {
      if (element.Id == id) {
        return element;
      } else if (element.nodes != null) {
        var i;
        var result = null;
        for (i = 0; result == null && i < element.nodes.length; i++) {
          result = this.searchTree(element.nodes[i], id);
        }
        return result;
      }
      return null;
    },
    searchstokTree(element, value) {
      if (element.Stokcode === value) {
        return element;
      } else if (element.nodes != null) {
        var i;
        var result = null;
        for (i = 0; result == null && i < element.nodes.length; i++) {
          result = this.searchstokTree(element.nodes[i], value);
        }
        return result;
      }
      return null;
    },
      searchenust(Stokcode){
   
    let result=this.searchstokTree(this.tree[0],Stokcode)
        if(result!=null){   
        if(this.Usttanim==="")
         this.Usttanim=result.UstStokTanim;
        else if(result.UstStokTanim!=="-")
        this.Usttanim=this.Usttanim+" - "+result.UstStokTanim;
     
        this.searchenust(result.UstStok)
       }
     },
    addtreenode() {
      let myself = this;
      console.log(myself.newtreestok);
      this.$http.post(`${state.ip}/api/admin/addnewtreenode`, {
          Stokcode: myself.newtreestok,
          Tanim: myself.newtreestokname,
          UstStok: myself.selectedStokCode,
          UstStokTanim: myself.selectedStokCodeTanim,
          ProjectCode: myself.project
        })
        .then(response => {
          if (response.data.success) {
            document.getElementById("addewtreenodecancel").click();
            var element = myself.tree;
            let result = this.searchstokTree(
              element[0],
              myself.selectedStokCode
            );

            let obj = {
              Id: response.data.Id,
              Stokcode: myself.newtreestok,
              Tanim: myself.newtreestokname,
              UstStok: myself.selectedStokCode,
              UstStokTanim: myself.selectedStokCodeTanim,
              SerinoTakip: 0,
              SeriNo: "",
              Ipaddress: "",
              IpTakip: this.newipcontrol,
              AltYapiTakip: this.newimalatcontrol,
              work: { count: 0 },
              progress: { count: 0 },
              nodes: []
            };
            result.nodes.push(obj);
            Swal.fire("", response.data.msg, "success");
            myself.newtreestok = "";
            myself.newtreestokname = "";
          } else {
            document.getElementById("addewtreenodecancel").click();
            Swal.fire("", response.msg, "error");
          }
        });
    },
    addproceessbegin() {
      this.addmi = true;
      this.editandnewheader = "Yeni İşlem Ekle";
      this.newtreestok = "";
      this.newtreestokname = "";
      this.newipcontrol = false;
      this.newimalatcontrol = false;
      this.newserinumbercontrol = false;
    },
    addtreeprocess() {
      this.$http.post(`${state.ip}/api/admin/addnewtreeprocess`, {
          Stokcode: this.newtreestok,
          Tanim: this.newtreestokname,
          UstStok: this.selectedStokCode,
          UstStokTanim: this.selectedStokCodeTanim,
          SerinoTakip: this.newserinumbercontrol,
          IpTakip: this.newipcontrol,
          AltYapiTakip: this.newimalatcontrol,
          ProjectCode: this.project
        })
        .then(response => {
          if (response.data.success) {
            document.getElementById("addewtreeproceescancel").click();
            var element = this.tree;
            let result = this.searchstokTree(element[0], this.selectedStokCode);

            let obj = {
              Id: response.data.Id,
              Stokcode: this.newtreestok,
              Tanim: this.newtreestokname,
              UstStok: this.selectedStokCode,
              UstStokTanim: this.selectedStokCodeTanim,
              SerinoTakip: Number(this.newserinumbercontrol),
              SeriNo: "",
              Ipaddress: "",
              color: "red",
              IpTakip: Number(this.newipcontrol),
              AltYapiTakip: Number(this.newimalatcontrol),
              work: { count: 0 },
              progress: { count: 0 }
            };
            result.nodes.push(obj);
            Swal.fire("", response.data.msg, "success");
            this.newtreestok = "";
            this.newtreestokname = "";
          } else {
            document.getElementById("addewtreeproceescancel").click();
            Swal.fire("", response.data.msg, "error");
          }
        });
    },

    deleteprocess() {
      this.dataspinner.removeAttribute("hidden");
      let UstStok = this.searchTree(this.tree[0], this.selecteddeleteid)
        .UstStok;
      console.log(this.selectedStokCode);
      this.$http.post(`${state.ip}/api/admin/deleteprocess`, {
          Stokcode: this.selectedStokCode,
          Id: this.selecteddeleteid
        })
        .then(response => {
          if (response.data.success) {
            document.getElementById("silemincancel").click();
            let result = this.searchstokTree(this.tree[0], UstStok);
            if(result!==null){
            result.nodes = result.nodes.filter(
              person => person.Id != this.selecteddeleteid
            );
            }
            Swal.fire("", response.data.msg, "success");
          } else {
            document.getElementById("silemincancel").click();
            Swal.fire("", response.data.msg, "error");
          }
          this.dataspinner.setAttribute("hidden", "");
        });
    },

    edittreenode() {
      this.addmi = false;
      this.editandnewheader = "Düzenle";
      let result = this.searchTree(this.tree[0], this.selecteddeleteid);
      this.newtreestok = result.Stokcode;
      this.newtreestokname = result.Tanim;
      this.newipcontrol = Boolean(result.IpTakip);
      this.newimalatcontrol = Boolean(result.AltYapiTakip);
      this.newserinumbercontrol = Boolean(result.SerinoTakip);
    },
    edittreenodesent() {
      this.$http.post(`${state.ip}/api/admin/updatetreeprocess`, {
          Stokcode: this.newtreestok,
          Tanim: this.newtreestokname,
          SerinoTakip: this.newserinumbercontrol,
          IpTakip: this.newipcontrol,
          AltYapiTakip: this.newimalatcontrol,
          Id: this.selecteddeleteid
        })
        .then(response => {
          document.getElementById("addewtreeproceescancel").click();
          if (response.data.success) {
            let result = this.searchTree(this.tree[0], this.selecteddeleteid);
            result.Stokcode = this.newtreestok;
            result.Tanim = this.newtreestokname;
            result.IpTakip = Number(this.newipcontrol);
            result.SerinoTakip = Number(this.newserinumbercontrol);
            result.AltYapiTakip = Number(this.newimalatcontrol);
            Swal.fire("", response.data.msg, "success");
          } else {
            document.getElementById("addewtreeproceescancel").click();
            Swal.fire("", response.data.msg, "error");
          }
        });
    },
    exportTable(){
       this.dataspinner = document.getElementById("spinner");
      this.dataspinner.removeAttribute("hidden");
      let myself = this;
      this.$http.post(`${state.ip}/api/admin/bomgetir`, {
          project: this.project
        })
        .then(response => {
          // eslint-disable-next-line no-console
           console.log(response);
           /* make the worksheet */
              var ws = XLSX.utils.json_to_sheet(response.data);
              
              /* add to workbook */
              var wb = XLSX.utils.book_new();
              XLSX.utils.book_append_sheet(wb, ws, this.project);
              
              /* write workbook */
              var wbout = XLSX.write(wb, {bookType:'xlsx', type:'array'}); // generate a nodejs buffer
              //var str = XLSX.write(wb, {bookType:'xlsx', type:'binary'}); // generate a binary string in web browser
              saveAs(new Blob([wbout],{type:"application/octet-stream"}), this.project+".xlsx");


           this.dataspinner.setAttribute("hidden", "");
        })
        .catch(function(error) {
          // eslint-disable-next-line no-console
          console.log(error);
        });
    }
  }
};
</script>

